var _this;

let USERID = uni.getStorageSync("USERID");
console.log('USERID=' + USERID)
var graceChecker = require("../../components/graceChecker/graceChecker.js");
export default {
	data() {
		return {
			AddApplyInfo: {
				user_id: '',
				user_intention: '',
				check_in_type: '',
				in_reason: '',
				date_check_in: '',
				date_move_out: '',
				apply_type: 0,
			},
			//授权订阅按钮
			subscribeText: "点击授权订阅",
			subscribeDisabled: false,
			//公寓数据
			apa_name: '', //选中的公寓名称
			modalName: null,
			apartmentList: [], //公寓列表
			selectApartmentBtnDisabled: true,
			selectApartmentBtnText: '无数据',

			//form表单提交数据按钮禁用状态
			SubmitButtonDisabled: this.SubmitButtonDisabled,




		}
	},
	onLoad() {

	},
	mounted() {
		_this = this;
		let USERID = uni.getStorageSync("USERID");
		_this.AddApplyInfo.user_id = USERID;
		var date = new Date();
		var seperator1 = "-";
		var year = date.getFullYear();
		var month = date.getMonth() + 1;
		var strDate = date.getDate();
		if (month >= 1 && month <= 9) {
			month = "0" + month;
		}
		if (strDate >= 0 && strDate <= 9) {
			strDate = "0" + strDate;
		}
		_this.AddApplyInfo.date_check_in = year + seperator1 + month + seperator1 + strDate;
		_this.AddApplyInfo.date_move_out = _this.AddApplyInfo.date_check_in;
		/* 判断用户是否存在已经提交但没审批的申请*/
		var allParams = {
			url: 'checkUserIsHaveAApplyNotAdopt',
			setUpUrl: true,
			type: 'get',
			data: {
				user_id: USERID
			},
			sCallback: function(data) {
				console.log(data);
				if (data.code == 1) {
					uni.showModal({
						content: "获取已提交但未审批的申请数据异常",
						confirmText: "确定",
						showCancel: false,
						success: function(res) {
							if (res.confirm) {
								uni.navigateBack({
									delta: 1
								});
							}
						}
					});
				} else if (data.code == 0) {
					if (data.info != 0) {
						uni.showModal({
							content: "存在未审批的请求(" + data.info + ")，请等待审批结束",
							confirmText: "确定",
							showCancel: false,
							success: function(res) {
								if (res.confirm) {
									uni.navigateBack({
										delta: 1
									});
								}
							}
						});
						return;
					}
				}
			}
		};
		_this.$base.request(allParams, _this);
		//获取所有公寓
		/**......**/
		var allParams = {
			url: 'queryAllApartmentInfoForList',
			setUpUrl: true,
			type: 'get',
			sCallback: function(data) {
				if (data.code == 1) {
					uni.showToast({
						title: '获取公寓数据异常',
						icon: 'none'
					})
				} else {
					if (data.data.length == 0) {
						uni.showToast({
							title: '公寓数据为空',
							icon: 'none'
						})
					} else {
						_this.selectApartmentBtnText = "选择公寓";
						_this.selectApartmentBtnDisabled = false;
						_this.apartmentList = data.data;
					}

				}
			}
		};
		_this.$base.request(allParams, _this);

	},
	methods: {
		subscribe(e) {
			wx.requestSubscribeMessage({
				tmplIds: ['JS2pyrbT1miKHnSfHbLwtha0GbioKnqtkA2cJ-ksEf4'],
				success(res) {
					var length = 0,
						agree = false;
					for (var ever in res) {
						if (ever == 'JS2pyrbT1miKHnSfHbLwtha0GbioKnqtkA2cJ-ksEf4') {
							console.log("ever:" + ever + ",res:" + res[ever]);
							if (res[ever] == 'accept') {
								_this.subscribeText = '已订阅本次推送';
								_this.subscribeDisabled = 'disabled';
							}
						}

						length++;
					}
				}
			})
		},
		//提交入住申请
		/**......**/
		formSubmitToAddApply: function(e) {
			var rule = [{
					name: "user_id",
					checkType: "string",
					checkRule: "6,12",
					errorMsg: "工号应为6-12个字符"
				},
				{
					name: "in_reason",
					checkType: "notnull",
					checkRule: "",
					errorMsg: "入住事由不可为空"
				},
				{
					name: "date_check_in",
					checkType: "notnull",
					checkRule: "",
					errorMsg: "入住日期不可为空"
				},
				{
					name: "date_move_out",
					checkType: "notnull",
					checkRule: "",
					errorMsg: "结束日期不可为空"
				},
				{
					name: "check_in_type",
					checkType: "in",
					checkRule: "0,1",
					errorMsg: "请选择住宿类型"
				},
				{
					name: "user_intention",
					checkType: "notnull",
					checkRule: "",
					errorMsg: "请选择公寓意向"
				},
			];
			//进行表单检查
			var formData = e.detail.value;
			var checkRes = graceChecker.check(formData, rule);
			if (checkRes) {
				var allParams = {
					url: 'addApplyInfoToIn',
					setUpUrl: true,
					type: 'get',
					data: {
						user_id: formData.user_id,
						user_intention: formData.user_intention,
						check_in_type: formData.check_in_type,
						in_reason: formData.in_reason,
						date_check_in: formData.date_check_in,
						date_move_out: formData.date_move_out,
						apply_type: formData.apply_type
					},
					sCallback: function(data) {
						console.log(data);
						if (data.code == 1) {
							uni.showToast({
								title: data.message,
								icon: 'none'
							})
						} else {
							uni.redirectTo({
								url: '../main/main',
								success() {
									uni.showToast({
										title: data.message,
										icon: 'none'
									})
								}
							})
						}
					}
				};
				_this.$base.request(allParams, _this);
			} else {
				uni.showToast({
					title: graceChecker.error,
					icon: "none"
				});
			}
		},
		//设置住宿类型
		SetCheckInType(e) {
			_this.AddApplyInfo.check_in_type = e.detail.value;
		},
		//根据用户输入的内容显示公寓信息
		showTheApartmentData(e) {
			console.log(e);
			//用户输入或删除任何数据，都清空用户意向input的值，防止误触提交
			_this.AddApplyInfo.user_intention = "";
			if (!e.value) {
				e.callback(_this.apartmentList)
			} else {
				let data = []
				_this.apartmentList.forEach(item => {
					if (item.apa_name.indexOf(e.value) > -1) {
						data.push(item)
					}
				})
				e.callback(data)
			}
		},
		//用户选择的意向公寓
		selectedTheApartmentData(value) {
			_this.AddApplyInfo.user_intention = value.apa_id;

		},
		//设置入住日期
		DateCheckIn(e) {
			_this.AddApplyInfo.date_check_in = e.detail.value;
		},
		//设置结束日期
		DateExpireChange(e) {
			_this.AddApplyInfo.date_move_out = e.detail.value;
		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		searchIcon(e) {
			let key = e.detail.value.toLowerCase();
			let list = _this.apartmentList;
			for (let i = 0; i < list.length; i++) {
				let a = key;
				let b = list[i].apa_name.toLowerCase();
				if (b.search(a) != -1) {
					list[i].isShow = true
				} else {
					list[i].isShow = false
				}
			}
			_this.apartmentList = list
		},
		clickItem(apa_name, apa_id) {
			_this.apa_name = apa_name;
			_this.AddApplyInfo.user_intention = apa_id;
			_this.hideModal();
		}
	}
}
